 import simpleAddress from "@/components/simple-address/simple-address.nvue"
 import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
			title: "添加地址",
			cityPickerValueDefault: [0, 0, 1],
			pickerText: '选择城市',
			addrMes:{
				name:"",
				phone:'',
				province:'',
				city:'',
				area:'',
				addres:'',// 详细地址
				isReading: true, // 防抖
			},
		}
	},
	onShow() {
		this.isReading = true
	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		openAddres() {
			this.$refs.simpleAddress.open();
		},
		onConfirm(e) {
			// console.log(e)
			this.pickerText = e.label // 地址label信息
			let box = e.label.split('-')
			console.log(box)
			this.addrMes.province = box[0]
			this.addrMes.city = box[1]
			this.addrMes.area = box[2]
		},
		// 地区选择器
		setCitys(val){
			console.log(val,'选择地区')
		},
		
		
		// 保存收获地址信息mes
		saveAddRess(){
			let _this = this
			let obj = this.addrMes
			obj.token = this.$store.state.userInfo.token
			console.log(obj)
			// console.log(this.$store.state.userInfo)
			if (!obj.name) {
				uni.showModal({
					title:"提示",
					showCancel:false,
					content:'请完善收货人'
				})
				return
			}
			if (!obj.phone) {
				uni.showModal({
					title:"提示",
					showCancel:false,
					content:'请完善手机号'
				})
				return
			}
			if (!obj.province) {
				uni.showModal({
					title:"提示",
					showCancel:false,
					content:'请完善地址信息'
				})
				return
			}
			if (!obj.addres) {
				uni.showModal({
					title:"提示",
					showCancel:false,
					content:'请完善详细地址'
				})
				return
			}
			// _this.$url 全局量
			if(this.isReading){
				this.isReading =false
				uni.request({
					url: _this.$http+'/api/user/addAddress',
					method:"POST",
					data: obj,
					success:function(res){
						console.log(res)
						if(res.data.code == 1){
							_this.isReading = true
							uni.showToast({
								title: '操作成功',
							});
							_this.addrMes = {
									name:"",
									phone:'',
									province:'',
									city:'',
									area:'',
									addres:''
								}
						} else {
							_this.isReading = true
							uni.showToast({
								title: res.data.msg,
								icon:'none'
							});
						}
					},
					
				})
			}
		},
		
		
		
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		// 返回上级目录
		goBack(){
			uni.navigateTo({
				url: '../myaddress/myaddress'
			})
		}
	},
	
	
	components: {
		simpleAddress,
		Topbar
	}
}
